@import "sizes";
@import url('https://fonts.googleapis.com/css?family=Merriweather');
@import url('https://fonts.googleapis.com/css?family=Lato');

.home-page {
  .news {
    background-color: #F9F9F9;
    padding: 50px 50px;
    h1 {
      margin-left: 170px;
      margin-bottom: 50px;
      font-family: Merriweather;
    }
    .main-new {
      background-color: white;
      font-family: lato;
      height: 300px;
      box-shadow: 0 2px 2px lightgrey;
      display: table;
      width: 100%;
      .left-new {
        width: 50%;
        height: 100%;
        display: table-cell;
        h2 {
          margin-left: initial;
          margin-bottom: initial;
          text-align: center;
        }
        h6 {
          margin: 10px 60px;
        }
        p {
          margin: 20px 30px;
        }
        .title-date {
          margin: 0 auto;
        }
      }
      .right-new {
        display: table-cell;
        width: 50%;
        height: 100%;
        .main-image {
          background-image: url("/img/favicon.png");
          width: 100%;
          height: 100%;
          background-size: cover;
          background-position: center center;
        }
      }
    }
  }
  .wrapper-news {
    margin-top: 25px;
    .second-new {
      background-color: white;
      font-family: lato;
      height: 200px;
      box-shadow: 0 2px 2px lightgrey;
      width: 32%;
      margin-top: 10px;
      height: 300px;
      .left-second-new {
        .second-image {
          width: 100%;
          height: 125px;
        }
      }
      &:nth-child(2), &:nth-child(3) {
        margin-left: 1.5%;
      }
    }
  }
}